import React from 'react';

function withSwipeToClose(WrappedComponent) {
    // ...and returns another component...
    return class extends React.Component {
      constructor(props) {
        super(props);
        
        this.trayRef = null;
        this.setTrayRef = element => { this.trayRef = element };
        this.containerRef = null;
        this.setContainerRef = element => { this.containerRef = element };
        this.contentRef = null;
        this.setContentRef = element => { this.contentRef = element };
    }

    componentDidMount() {
        this.initGesture();
    }

    initGesture() {
        let startDiffY = 0;
        let diffY = 0;
        let isGesture = false;
        let requiredDiffY = this.trayRef.clientHeight / 3;

        const handleTouchStart = (e) => {
            if (this.props.open && this.contentRef.scrollTop <= 0) {
                isGesture = true;
            } else {
                isGesture = false;
            }

            startDiffY = e.touches[0].pageY;
            this.trayRef.style.transition = 'none';
        }
    
        const handleTouchMove = (e) => {
            if (isGesture) {
                this.contentRef.ontouchmove = () => { e.preventDefault() };
            } else {
                this.contentRef.ontouchmove = null;
                return;
            }

            diffY = e.touches[0].pageY - startDiffY;
            console.log(diffY);

            if (this.contentRef.scrollTop > 0) {
                isGesture = false;
            }

            if (diffY > 0) {
                this.trayRef.style.transform = `translateY(${diffY}px)`;
            }
        }

        const handleTouchEnd = (e) => {
            this.trayRef.style.transform = '';
            this.trayRef.style.transition = '';

            if (diffY > requiredDiffY) {
                this.props.onClose();
            }
        }

        const handleTouchCancel = (e) => {
            console.log(e.target);
        }

        this.containerRef.addEventListener('touchstart', handleTouchStart);
        this.containerRef.addEventListener('touchmove', handleTouchMove);
        this.containerRef.addEventListener('touchend', handleTouchEnd);
        this.containerRef.addEventListener('touchcancel', handleTouchCancel);
    }
  
    render() {
        // ... and renders the wrapped component with the fresh data!
        // Notice that we pass through any additional props

        // console.log(this.ref.current);

        return <WrappedComponent
            {...this.props}
            ref={this.ref}
            setTrayRef={this.setTrayRef}
            setContainerRef={this.setContainerRef}
            setContentRef={this.setContentRef}
        />;
      }
    };
  }

export default withSwipeToClose;
